.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

h1 {
    padding: 50px;
}

.user-list {
    float: right;
    padding-right: 50px;
    width: 20%;
    padding-bottom: 100px;
}

.message-list {
    float: left;
    padding-left: 50px;
    width: 70%;
    padding-bottom: 100px;
}
